.knowledge {

    &__list {
        list-style: none;
        margin-top: 40px;
    }

    &__item {
        width: 319px;
        margin-right: 37px;

        &:last-child {
            margin-right: 0;
        }
    }

    &__img {
        display: block;
        margin-bottom: 20px;

        @media (max-width: $tablet-width) {
            margin-bottom: 18px;
        }
    }

    &__header {
        font-family: $second-font;
        font-size: 26px;
        font-weight: 500;
        width: 230px;
        line-height: 28px;
        text-transform: uppercase;

        @media (max-width: $tablet-width) {
            line-height: 26px;
            font-size: 16px;
        }
    }

    &__header-container {
        min-height: 160px;

        @media (max-width: $tablet-width) {
            min-height: 100px;
        }
    }

    &__description {
        width: 300px;
        font-size: 18px;
        font-weight: 400;
        margin-top: 8px;
        margin-bottom: 0;
        @media (max-width: $tablet-width) {
            width: 209px;
            font-size: 13px;
        }
    }

    &__link {
        display: block;
        position: relative;
        margin: 0;
        margin-top: 10px;
        color: $basic-black;
        text-transform: uppercase;
        font-family: $second-font;
        font-size: 23px;
        font-weight: 500;
        cursor: pointer;

        @media (max-width: $tablet-width) {
            font-size: 16px;
        }

        &::after {
            content: "";
            display: block;
            position: absolute;
            top: 0;
            left: 25%;
            width: 31px;
            height: 31px;
            background-image: url("../../img/arrow-right.png");
            background-repeat: no-repeat;

            // @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
            //   background-image: url("../../img/arrow-right@2x.png");
            //   width: 54px;
            //   height: 54px;
            // }

            @media (max-width: $tablet-width) {
                left: 18%;
                width: 22px;
                height: 22px;
                background-image: url("../../img/arrow-right-mobile.png");

                @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
                    background-image: url("../../img/arrow-right-mobile@2x.png");
                    width: 137px;
                    height: 137px;
                }
            }
        }
    }
}