.news-item {
    position: relative;
    width: 426px;
    margin-bottom: 137px;
    background-color: $basic-white;
    margin-left: auto;
    margin-right: auto;
    @media (max-width: 1340px) {
        width: 100%;
        padding-top: 20px;
        margin-bottom: 50px;
    }
    @media (max-width: 890px) {
        margin-bottom: 75px;
    }
    @media (max-width: 350px) {
        padding-top: 0;
    }
  
    &__img {
        display: block;

        @media (max-width: $tablet-width) {
            width: 309px;
            height: 154px;
        }

        @media (max-width: 1340px) {
            margin-left: auto;
            margin-right: auto;
        }
    }

    &__header-container {
        padding: 20px 50px 20px 30px;

        @media (max-width: $tablet-width) {
            padding: 15px 40px 10px 15px;
        }
    }

    &__header {
        font-family: $second-font;
        font-size: 21px;
        font-weight: 500;
        line-height: 24px;
        text-transform: uppercase;

        @media (max-width: $tablet-width) {
            letter-spacing: 0;
            font-size: 15px;
        }
    }

    &__description {
        font-size: 16px;
        font-weight: 400;

        @media (max-width: $tablet-width) {
            font-size: 12px;
        }
    }

    &__link {
        display: block;
        position: relative;
        margin: 0;
        padding: 15px 0 15px 30px;
        cursor: pointer;
        background-color: #f3f4f4;
        font-family: $second-font;
        letter-spacing: 2px;
        font-size: 19px;
        font-weight: 500;

        @media (max-width: $tablet-width) {
            padding: 10px 0 10px 10px;
            letter-spacing: 1px;
            font-size: 14px;
        }

        &:hover {
            opacity: 0.9;
        }

        &::after {
            content: "";
            display: block;
            position: absolute;
            z-index: 3;
            top: 10px;
            right: 70px;
            width: 32px;
            height: 32px;
            background-image: url("../../img/news-arrow-white.png");
            background-repeat: no-repeat;

            @media (max-width: $tablet-width) {
                top: 10px;
                right: 41px;
                background-image: url("../../img/news-arrow-white-mobile.png");
                background-size: 32px 32px;
            }

            @media (min-resolution: $retina-dpi),
                (min-resolution: $retina-dppx) {
                background-image: url("../../img/news-arrow-white@2x.png");
                background-size: 23px 23px;
            }

            @media (max-width: 768px),
                (min-resolution: $retina-dpi),
      (min-resolution: $retina-dppx) {
                background-image: url("../../img/news-arrow-white-mobile@2x.png");
            }
        }

        &::before {
            content: "";
            display: block;
            position: absolute;
            right: 20px;
            bottom: -10px;
            width: 180px;
            height: 71px;
            opacity: 0.9;
            background-image: url("../../img/green-button.png");
            background-size: 180px 71px;
            background-repeat: no-repeat;

            @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
                background-image: url("../../img/green-button@2x.png");
            }

            @media (max-width: $tablet-width) {
                right: -37px;
                bottom: -28px;
                background-image: url("../../img/green-button-mobile.png");
                background-size: 131px 52px;

                @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
                    background-image: url("../../img/green-button-mobile@2x.png");
                }
            }
        }
    }
}