body,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
dd {
    margin: 0;
    padding: 0;
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    font-size: 29px;
    line-height: 1.2em;
    font-family: "MyriadPro", "Arial", sans-serif;
    background-color: #f9fafa;
}

main {
    flex-grow: 1;
}

.visually-hidden {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;

    clip: rect(0 0 0 0);
    overflow: hidden;
}

.container {
    box-sizing: border-box;
    width: 100%;
    max-width: 1630px;
    padding: 0 60px;
    margin: 0 auto;

    @media (max-width: 768px) {
        padding: 0 35px;
    }
}

.page {
    &__subtitle {
        position: relative;
        font-family: $second-font;
        font-size: 39px;
        font-weight: 500;
        text-align: center;
        text-transform: uppercase;

        @media (max-width: 1340px) {
            margin-bottom: 30px;
        }

        @media (max-width: $tablet-width) {
            font-size: 20px;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 30px;
        }

        @media (max-width: 916px) {
            width: 80%;
        }

        &::after {
            content: "";
            display: block;
            position: relative;
            top: 5px;
            left: 47%;
            width: 135px;
            height: 2px;
            background-color: #0a6d36;

            @media (max-width: $tablet-width) {
                left: 48%;
                width: 55px;
                height: 1px;
            }
        }
    }
}

.main-title {
    margin-top: 50px;
    position: relative;
    line-height: 1em;
    font-size: 55px;
    text-align: center;
    text-transform: uppercase;
    font-weight: 500;
    font-family: "Oswald", "Open Sans", sans-serif;
    align-self: center;

    &:before,
    &:after {
        position: absolute;
        content: "";
        top: 50%;
        width: 250px;
        height: 4px;
        background-color: #0a6d24;

        @media (max-width: 900px) {
            width: 80px;
        }

        @media (max-width: 540px) {
            height: 3px;
        }
    }

    &:before {
        right: calc(100% + 40px);

        @media (max-width: 540px) {
            right: calc(100% + 15px);
        }
    }
    &:after {
        left: calc(100% + 40px);

        @media (max-width: 540px) {
            left: calc(100% + 15px);
        }
    }

    @media (max-width: 768px) {
        font-size: 38px;
    }

    @media (max-width: 540px) {
        font-size: 20px;
    }
}

.subtitle {
    line-height: 1em;
    font-size: 52px;
    text-transform: uppercase;
    font-weight: 500;
    font-family: "Oswald", "Open Sans", sans-serif;

    @media (max-width: 768px) {
        font-size: 35px;
    }

    @media (max-width: 540px) {
        font-size: 17px;
    }
}
