.site-banner {
    color: #ffffff;
    margin-top: 32px;

    @media (max-width: 1630px) {
        max-width: 100%;
        padding: 0;
    }

    @media (max-width: 768px) {
        margin-top: 24px;
        padding: 0;
    }

    @media (max-width: $tablet-width) {
        margin-top: 10px;
    }

    &__content-wrapper {
        padding-left: 145px;
        padding-bottom: 40px;
        background-color: #2c2a0c;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;

        @media (max-width: 1630px) {
            padding-right: 60px;
            padding-left: 60px;
        }

        @media (max-width: 768px) {
            padding-right: 35px;
            padding-left: 35px;
        }

        @media (max-width: $tablet-width) {
            padding-bottom: 20px;
        }
    }

    &__image-wrapper {
        position: relative;
        top: -32px;
        margin-bottom: 44px;
        width: 380px;
        height: 370px;
        background-image: url("../img/banner-img.jpg");
        background-size: cover;
        box-shadow: 20px 20px 0 0 #ffffff;

        @media (max-width: 768px) {
            top: -24px;
            margin-bottom: 24px;
            width: 44%;
            height: auto;
            box-shadow: 8px 8px 0 0 #ffffff;
        }

        @media (max-width: $tablet-width) {
            top: -10px;
            margin-bottom: 10px;
        }

        &::after {
            @media (max-width: 768px) {
                content: "";
                display: block;
                padding-bottom: 100%;
            }
        }
    }

    &__text {
        margin-bottom: 27px;
        /* stylelint-disable */
        font-family: "MagistralCond", "Arial", sans-serif;
        /* stylelint-enable */
        font-size: 77px;
        font-weight: 700;
        text-transform: uppercase;

        @media (max-width: 768px) {
            font-size: 64px;
            margin-bottom: 20px;
        }

        @media (max-width: $tablet-width) {
            font-size: 30px;
            margin-bottom: 0;
        }
    }

    &__highlight {
        color: #25a84d;
    }

    &__image-label {
        display: block;

        @media (max-width: 768px) {
            width: 68%;
            height: auto;
        }
    }
}
