// Для фона ///////////////////////////////////
.news {
    background-color: #b2f7ec7e;

    ///////////////////////////////////////////////
    &__container {
        display: flex;
        align-items: flex-start;
        position: relative;
        margin-left: auto;
        margin-right: auto;

        @media (max-width: 1340px) {
            flex-wrap: wrap;
        }

        @media (max-width: $tablet-width) {
            flex-direction: column;
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 30px;

        @media (max-width: 1340px) {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-column-gap: 30px;
        }

        @media (max-width: 900px) {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }

        &:last-child {
            @media (max-width: 900px) {
                display: none;
            }
        }

        &_middle {
            margin-top: 150px;

            @media (max-width: 1340px) {
                margin-top: 0;
            }
        }
    }
}