.products {
    &__wrapper {
        display: flex;
        flex-direction: column;
    }

    &__feeds-title {
        margin-bottom: 35px;

        @media (max-width: 1300px) {
            text-align: center;
        }

        @media (max-width: 540px) {
            margin-bottom: 20px;
            text-align: left;
        }

        @media (max-width: 440px) {
            margin-bottom: 16px;
        }
    }

    &__health-title {
        margin-bottom: 90px;
        text-align: center;

        @media (max-width: 768px) {
            margin-bottom: 48px;
        }

        @media (max-width: 540px) {
            margin-bottom: 24px;
        }
    }

    &__feeds {
        padding-top: 95px;
        padding-bottom: 120px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media (max-width: 1300px) {
            flex-direction: column;
            padding-bottom: 80px;
        }

        @media (max-width: 540px) {
            padding-top: 40px;
            padding-bottom: 32px;
        }
    }

    &__feeds-description {
        display: grid;
        grid-template-columns: 51% 1fr;

        @media (max-width: 1300px) {
            display: flex;
            flex-direction: column;
        }
    }

    &__feeds-text {
        margin: 0;
        line-height: 1em;
        font-size: 30px;

        @media (max-width: 1300px) {
            margin-bottom: 60px;
        }

        @media (max-width: 768px) {
            font-size: 20px;
        }

        @media (max-width: 540px) {
            margin-bottom: 16px;
            font-size: 12px;
        }
    }

    &__feeds-img {
        display: block;
        align-self: center;
        height: auto;
        margin-left: auto;
        grid-column-start: 2;
        grid-row-start: 1;
        grid-row-end: 3;
        grid-column-end: 3;

        &_mobile {
            display: none;

            @media (max-width: 540px) {
                display: block;
                margin-left: 16px;
                margin-bottom: 16px;
                width: 150px;
                float: right;
                height: auto;
            }
        }

        @media (max-width: 1400px) {
            width: 100%;
        }

        @media (max-width: 540px) {
            display: none;
        }
    }

    &__health-wrapper {
        padding-top: 120px;
        background-color: #ffffff;

        @media (max-width: 1300px) {
            padding-top: 70px;
        }

        @media (max-width: 540px) {
            padding-top: 32px;
        }
    }

    &__inner-container {
        margin-left: auto;
        margin-right: auto;
        max-width: 1590px;
        padding: 0 50px;

        @media (max-width: 1700px) {
            max-width: 1200px;
        }

        @media (max-width: 1200px) {
            max-width: inherit;
        }

        @media (max-width: 768px) {
            padding-bottom: 48px;
        }

        @media (max-width: 540px) {
            padding-bottom: 16px;
            padding-left: 35px;
            padding-right: 35px;
        }
    }

    &__health-list {
        margin-left: auto;
        margin-right: auto;
        list-style-type: none;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        text-transform: uppercase;
        font-weight: 500;
        /* stylelint-disable */
        font-family: "Oswald", "Open Sans", sans-serif;
        /* stylelint-enable */

        @media (max-width: 1200px) {
            align-items: center;
            max-width: 700px;
        }

        @media (max-width: 800px) {
            // max-width: 440px;
        }

        @media (max-width: 540px) {
            flex-direction: row;
        }
    }

    &__health-item {
        position: relative;
        margin-bottom: 75px;
        width: 530px;
        height: 222px;
        line-height: 1.2em;
        font-size: 27px;
        background-image: url("../img/products-frame.svg");
        background-repeat: no-repeat;
        background-size: contain;

        @media (max-width: 1200px) {
            width: 350px;
            height: 146px;
            font-size: 18px;
        }

        @media (max-width: 800px) {
            margin-bottom: 40px;
            width: 220px;
            height: 93px;
            font-size: 11px;
        }

        @media (max-width: 540px) {
            margin-bottom: 10px;
            width: 150px;
            height: 70px;
            font-size: 8px;
        }

        &:before {
            content: "";
            position: absolute;
            background-color: $basic-white;
            top: -3px;
            left: 150px;
            width: 116px;
            height: 10px;

            @media (max-width: 1200px) {
                left: 100px;
                width: 80px;
            }

            @media (max-width: 800px) {
                left: 60px;
                width: 50px;
            }

            @media (max-width: 540px) {
                left: 45px;
                width: 30px;
            }
        }

        &:after {
            content: "";
            position: absolute;
            right: 170px;
            bottom: -35px;
            width: 110px;
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            background-color: $basic-white;

            @media (max-width: 1200px) {
                right: 100px;
                bottom: -22px;
                width: 75px;
            }

            @media (max-width: 800px) {
                right: 70px;
                bottom: -15px;
                width: 50px;
            }

            @media (max-width: 540px) {
                right: 45px;
                bottom: -3px;
                width: 35px;
            }
        }

        &_efficiency {
            &:after {
                height: 75px;
                background-image: url("../img/efficiency.svg");

                @media (max-width: 1200px) {
                    height: 50px;
                }

                @media (max-width: 800px) {
                    height: 35px;
                }

                @media (max-width: 540px) {
                    height: 23px;
                }
            }
        }

        &_cow {
            &:after {
                height: 75px;
                background-image: url("../img/cow.svg");

                @media (max-width: 1200px) {
                    height: 55px;
                }

                @media (max-width: 800px) {
                    height: 35px;
                }

                @media (max-width: 540px) {
                    height: 23px;
                }
            }
        }

        &_prevention {
            &:after {
                height: 65px;
                background-image: url("../img/prevention.svg");

                @media (max-width: 1200px) {
                    height: 44px;
                }

                @media (max-width: 800px) {
                    height: 32px;
                }

                @media (max-width: 540px) {
                    height: 23px;
                }
            }
        }

        &_birth {
            &:after {
                height: 85px;
                background-image: url("../img/birth.svg");

                @media (max-width: 1200px) {
                    height: 58px;
                }

                @media (max-width: 800px) {
                    height: 39px;
                }

                @media (max-width: 540px) {
                    height: 26px;
                }
            }
        }

        &_reproductive {
            &:after {
                height: 83px;
                background-image: url("../img/reproductive.svg");

                @media (max-width: 1200px) {
                    height: 57px;
                }

                @media (max-width: 800px) {
                    height: 38px;
                }

                @media (max-width: 540px) {
                    height: 23px;
                }
            }
        }

        &_ketosis {
            &:after {
                height: 96px;
                background-image: url("../img/ketosis.svg");

                @media (max-width: 1200px) {
                    height: 65px;
                }

                @media (max-width: 800px) {
                    height: 44px;
                }

                @media (max-width: 540px) {
                    height: 27px;
                }
            }
        }

        &_cattle {
            &:after {
                height: 64px;
                background-image: url("../img/cattle.svg");

                @media (max-width: 1200px) {
                    height: 43px;
                }

                @media (max-width: 800px) {
                    height: 29px;
                }

                @media (max-width: 540px) {
                    height: 19px;
                }
            }
        }

        &_quality {
            &:after {
                height: 86px;
                background-image: url("../img/quality.svg");

                @media (max-width: 1200px) {
                    height: 59px;
                }

                @media (max-width: 800px) {
                    height: 39px;
                }

                @media (max-width: 540px) {
                    height: 26px;
                }
            }
        }

        &_hormones {
            &:after {
                height: 95px;
                background-image: url("../img/hormones.svg");

                @media (max-width: 1200px) {
                    height: 65px;
                }

                @media (max-width: 800px) {
                    height: 44px;
                }

                @media (max-width: 540px) {
                    height: 27px;
                }
            }
        }
    }

    &__health-text {
        margin: 0;
        padding-top: 45px;
        padding-bottom: 45px;
        padding-left: 100px;

        @media (max-width: 1200px) {
            padding-top: 30px;
            padding-bottom: 0;
            padding-left: 60px;
        }
        @media (max-width: 800px) {
            padding-top: 15px;
            padding-left: 40px;
        }

        @media (max-width: 540px) {
            padding-top: 12px;
            padding-bottom: 0;
            padding-left: 26px;
        }
    }

    &__links-wrapper {
        background-color: #f9fafa;
    }

    &__links-list {
        display: flex;
        justify-content: space-between;
        margin-left: auto;
        margin-right: auto;
        padding-top: 90px;
        padding-bottom: 190px;
        max-width: 1400px;
        background-color: #f9fafa;

        @media (max-width: 1700px) {
            max-width: 1000px;
        }

        @media (max-width: 1300px) {
            padding-bottom: 120px;
        }

        @media (max-width: 1200px) {
            padding-left: 50px;
            padding-right: 50px;
            max-width: 700px;
        }

        @media (max-width: 800px) {
            max-width: 500px;
        }

        @media (max-width: 768px) {
            padding-top: 64px;
            padding-bottom: 96px;
            padding-left: 35px;
            padding-right: 35px;
        }

        @media (max-width: 540px) {
            flex-direction: row;
            padding-top: 32px;
            padding-bottom: 64px;
            max-width: 300px;
        }
    }

    &__link {
        display: inline-block;
        padding: 40px 50px;
        color: $basic-white;
        background-color: #0a6d24;
        line-height: 1em;
        font-size: 43px;
        font-weight: 400;
        /* stylelint-disable */
        font-family: "Oswald", "Open Sans", sans-serif;
        /* stylelint-enable */
        text-transform: uppercase;
        text-align: center;
        text-decoration: none;

        @media (max-width: 1200px) {
            max-width: 300px;
            padding: 20px 0;
        }

        @media (max-width: 800px) {
            max-width: 210px;
            padding: 15px 0;
            font-size: 30px;
        }

        @media (max-width: 540px) {
            max-width: 130px;
            padding: 10px 0;
            font-size: 17px;

            &:first-of-type {
                margin-bottom: 0;
            }
        }
    }
}
