.header {
    $root: &;

    font-size: 21px;
    background-color: #ffffff;

    &__nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__nav-list {
        list-style: none;
        display: flex;

        @media (max-width: $tablet-width) {
            font-size: 12px;
            align-items: center;
        }
    }

    &__nav-item {
        position: relative;
        text-align: end;
        margin-left: 30px;

        @media (max-width: $tablet-width) {
            margin-left: 15px;
        }

        @media (max-width: 374px) {
            margin-left: 8px;
        }

        &_feedback {
            @media (max-width: $desktop-width) {
                display: none;
            }
        }

        &_search {
            @media (max-width: 374px) {
                display: none;
            }
        }
    }

    &__submenu {
        display: none;
        position: absolute;
        z-index: 2;
        top: 100%;
        right: 0;
        min-width: 260px;
        padding: 30px 20px;
        text-align: right;
        line-height: 1em;
        background-color: #ffffff;
        box-shadow: 0 0 13.3px 0.7px rgba(0, 2, 5, 0.16);

        @media (max-width: $tablet-width) {
            min-width: 120px;
            padding: 15px;
        }

        &.opened {
            display: block;
        }
    }

    &__sub-list {
        list-style: none;
    }

    &__nav-link {
        cursor: pointer;
        display: block;
        color: inherit;
        text-decoration: none;
        position: relative;
        background-repeat: no-repeat;
        transition: color $transition-duration;

        &:hover {
            color: rgba(0, 0, 0, 0.7);
        }

        &_sub {
            padding-right: 21px;

            @media (max-width: $tablet-width) {
                padding-right: 16px;
            }

            &::before {
                content: "";
                position: absolute;
                top: 33%;
                right: 4px;
                width: 6px;
                height: 6px;
                transform: rotate(-45deg);
                transition: transform $transition-duration;
                border-left: 2px solid #000000;
                border-bottom: 2px solid #000000;

                @media (max-width: $tablet-width) {
                    top: 38%;
                    width: 4px;
                    height: 4px;
                    border-width: 1px;
                }
            }

            &.opened::before {
                transform: rotate(-45deg) scale(-1, -1) translate(3px, -3px);

                @media (max-width: $tablet-width) {
                    transform: rotate(-45deg) scale(-1, -1) translate(2px, -2px);
                }
            }
        }

        &_search {
            width: 25px;
            height: 34px;
            background-image: url("../img/search.png");
            background-position: center center;
            background-size: 20px auto;
            transition: opacity $transition-duration;

            @media (max-width: $tablet-width) {
                width: 16px;
                background-size: contain;
            }

            &:hover {
                opacity: 0.7;
            }
        }

        &_feedback {
            border: 2px solid $basic-green;
            padding: 2px 20px 0 20px;
            text-transform: uppercase;
            line-height: 1.2em;
        }

        &_menu {
            width: 40px;
            height: 100%;
            transition: opacity $transition-duration;

            @media (max-width: $tablet-width) {
                width: 25px;
                height: 34px;
            }

            &:hover {
                opacity: 0.7;
            }

            &::before, &::after {
                content: "";
            }

            &::before,
            &::after,
            #{$root}__line {
                position: absolute;
                left: 0;
                width: 100%;
                height: 2px;
                background-color: #000000;
                transition: transform $transition-duration;
            }

            &::before {
                top: 6px;

                @media (max-width: $tablet-width) {
                    top: 10px;
                }
            }

            &::after {
                bottom: 6px;

                @media (max-width: $tablet-width) {
                    bottom: 10px;
                }
            }

            #{$root}__line {
                top: 50%;
                transform: translateY(-50%);
            }

            &.opened {
                #{$root}__line {
                    transform: translateY(-50%) scaleX(0);
                }

                &::before {
                    top: 16px;
                    transform: rotate(45deg);
                }

                &::after {
                    bottom: 16px;
                    transform: rotate(-45deg);
                }
            }
        }
    }

    &__sub-item {
        margin-bottom: 10px;
        overflow: hidden;

        @media (max-width: $tablet-width) {
            margin-bottom: 4px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__submenu-link {
        display: inline-block;
        position: relative;
        padding: 4px 0;
        color: $basic-black;
        text-decoration: none;

        &::after {
            content: "";
            position: absolute;
            right: 0;
            bottom: 0;
            width: 110%;
            height: 1px;
            transform: scaleX(0);
            transition: transform $transition-duration;
            transform-origin: bottom right;
            background-color: $basic-green;
        }

        &:hover::after {
            transform: scaleX(1);
        }
    }

    &__container {
        margin: 0 auto;
        padding: 20px 35px;

        @media (max-width: $desktop-width) {
            padding: 15px 20px;
        }

        @media (max-width: 374px) {
            padding: 8px 15px;
        }
    }

    &__logo-image {
        @media (max-width: $desktop-width) {
            width: 100%;
            min-width: 70px;
            max-width: 20%;
            height: auto;
        }
    }

    &__logo {
        display: contents;
    }
}
