.footer {
    font-size: 19px;
    color: #393837;

    @media (max-width: $tablet-width) {
        font-size: 15px;
    }

    &__content-wrapper {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        padding: 85px 60px;
        background-color: $basic-grey;

        @media (max-width: $desktop-width) {
            flex-direction: column;
            padding: 60px;
        }

        @media (max-width: 768px) {
            padding: 40px 20px;
        }

        @media (max-width: $tablet-width) {
            padding-top: 24px;
        }
    }

    &__logo-wrapper, &__contacts-wrapper {
        width: 300px;

        @media (max-width: $desktop-width) {
            width: 100%;
        }
    }

    &__logo-wrapper {
        @media (max-width: $desktop-width) {
            margin-bottom: 24px;
        }

        @media (max-width: $tablet-width) {
            margin-bottom: 16px;
            text-align: center;
        }
    }

    &__logo-link {
        display: inline-block;

        @media (max-width: 768px) {
            width: 160px;
        }

        @media (max-width: $tablet-width) {
            width: 120px;
        }
    }

    &__logo-image {
        display: block;

        @media (max-width: $desktop-width) {
            width: 100%;
            height: auto;
        }
    }

    &__nav-list {
        list-style: none;
        columns: 2;
        column-gap: 48px;
        margin: 0 40px;
        margin-top: -7px;
        text-align: center;

        @media (max-width: 1300px) {
            display: none;
        }
    }

    &__nav-link, &__contacts-link {
        text-decoration: none;
        transition: opacity $transition-duration;

        &:hover {
            opacity: 0.7;
        }
    }

    &__nav-link {
        color: inherit;
    }

    &__contacts-wrapper {
        margin: 0;
        text-align: right;
        line-height: 1em;

        @media (max-width: $desktop-width) {
            text-align: left;
        }
    }

    &__contacts-title {
        margin-top: 20px;
        margin-bottom: 4px;

        @media (max-width: $tablet-width) {
            margin-top: 16px;
        }

        &:first-child {
            margin-top: 0;
        }
    }

    &__contacts-link {
        color: #7e7e7d;
    }
}
